import { Nav } from './Nav';
import { About,  } from '../pages/About';
import { Projects } from '../pages/Projects';
import { Skills } from '../pages/Skills';
import { Main } from '../pages/Main';
import { useEffect, useRef } from 'react';



export const Layout = ()=>{

  const projectRef = useRef();
  const skillRef = useRef();
  

  const handleWheel = (e)=>{
    e.preventDefault();
    
    const firstscrollAmount = skillRef.current.getBoundingClientRect().top - 200
    const secondscrollAmount = projectRef.current.getBoundingClientRect().top
    
    const delta = ()=>{
      if(window.scrollY < 500){
        if(e.deltaY > 0){
          return firstscrollAmount
        }else{
          return -(firstscrollAmount)
        }
      }else if(firstscrollAmount < 1000 && firstscrollAmount > -800){
        if(e.deltaY > 0){
          console.log('스킬페이지에서 내렸을떄')
          return secondscrollAmount
        }else{
          console.log('스킬페이지에서 올렸을떄')
          return -secondscrollAmount-100
        }
      }else if(secondscrollAmount < 1000){
        if(e.deltaY > 0){
          console.log('맨밑에서 휠 내렸을때')
          return -firstscrollAmount
        }else{
          console.log('맨밑에서 휠 올렸을때')
          return firstscrollAmount
        }
      }
    }
        
    window.scrollBy({
      top : delta(),
      left : 0,
      behavior : 'smooth'
    })
    console.log(firstscrollAmount,1)
    console.log(secondscrollAmount,2)
  }
  

  useEffect(()=>{
    window.addEventListener('wheel', handleWheel, {passive : false})
    return () => {
      window.removeEventListener('wheel',handleWheel)
    }
  },[])

  return(
    <>
      <Main/>
      <Skills skillRef = {skillRef}/>
      <Projects projectRef={projectRef}/>
    </>
  )
}